* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color: #333;
  text-decoration: none;
}

@font-face {
  font-family: 'Courgette';
  font-style: normal;
  src: url(./Fonts/Courgette/Courgette-Regular.ttf);
}

@font-face {
  font-family: 'Yanone';
  font-style: normal;
  src: url(./Fonts/Yanone_Kaffeesatz/YanoneKaffeesatz-Regular.ttf);
}

.yanone {
  font-family: 'Yanone';
}

.courgette {
  font-family: 'Courgette';
}

.nav:hover {
  background-color: pink;
}

.gridImage:hover {
  transform: translateY(2px);
}

html {
  height: 100%;
  overflow: hidden;
}

body {
  margin: 0;
  padding: 0;
  perspective: 1px;
  transform-style: preserve-3d;
  height: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  overflow-x: hidden;
}

.header, .header:before {
  background: 50% 50% / cover;
} 

.header::before {
  bottom: 0;
  content: "";
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  background-image: url("./Images/HeaderImageDesktop.webp");
  background-size: cover;
  transform-origin: center center 0;
  transform: translateZ(-1px) scale(2);
  z-index: -1;
  min-height: 100vh;
}

.header * {
  text-align: center;
  margin: 0;
  padding: 1em 0;
}

@media only screen and (max-width: 600px) {
  .header::before {
    background-image: url("./Images/HeaderImageMobile.webp");
    transform: translateZ(0px) scale(1);
    /* filter: blur(2px); */
  }
}